.App {
  width: 100%;
  min-height: 100vh;
  background-color: var(--color-general-bg);
  position: relative;
}
.container {
  width: 60%;
  margin: 0 auto;
  padding-top: 2%;
}
@media screen and (max-width: 100rem) {
  .container {
    width: 80%;
  }
}
@media screen and (max-width: 70rem) {
  .container {
    width: 96%;
  }
}
